.app__navbar {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #14362b ;
    background: linear-gradient(90deg, #14362b 0%, #dca767ca 90%);
    background: -webkit-linear-gradient(90deg, #14362b 0%, #dca767ca 90%);
    background: -moz-linear-gradient(90deg, #14362b 0%, #dca767ca 90%);
    padding: 2rem;
    position: fixed;
    z-index: 10;
}

.app__navbar-logo {
    cursor: pointer;
}

.app__navbar-logo img {
    width: 150px;
}

.app__navbar-links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    list-style: none;
}

.app__navbar-links li {
    margin: 0 1rem;
    cursor: pointer;
}

.app__navbar-links li:hover {
    color: var(--color-grey);
}


.app__navbar-gallery a {
    margin: 0 1rem;
    text-decoration: none;
    transition: 0.5s ease;
}

.app__navbar-smallscreen {
    display: none;
}

.app__navbar-smallscreen_overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--color-green);
    transition: 0.5s ease;

    flex-direction: column;
    z-index: 5;
}

.app__navbar-smallscreen .overlay__btn {
    color: var(--color-blue);
    cursor: pointer;
}

.app__navbar-smallscreen_overlay .overlay__close {
    color: var(--color-blue);
    cursor: pointer;
    position: absolute;
    top: 2rem;
    right: 2rem;
}

.app__navbar-smallscreen_links {
    list-style: none;
}

.app__navbar-smallscreen_links li {
    margin: 2rem;
    cursor: pointer;
    color: var(--color-blue);
    font-size: 2rem;
    text-align: center;
    font-family: var(--font-base);
}

.app__navbar-smallscreen_links li:hover {
    color: var(--color-white);
}

@media screen and (max-width: 2000px) {
    .app__navbar-logo img {
        width: 210px;
    }
}

@media screen and (max-width: 1150px) {
    .app__navbar {
        justify-content: space-between;
    }

    .app__navbar-links {
        display: none;
    }

    .app__navbar-smallscreen {
        display: flex;
    }

    .app__navbar-logo img {
        width: 170px;
    }

}

@media screen and (max-width: 650px) {
    .app__navbar {
        justify-content: space-between;
        padding: 0.5rem 2rem;
    }

    .app__navbar-logo img {
        width: 100px;
        padding: 1rem 0;
    }

    .app__navbar-gallery {
        display: none;
    }
}